import React from "react";
import Context from "../../../Context";
import { injectIntl } from "react-intl";
import { Row, Col } from "antd";
import key from "../../../util/key";
import { SearchOutlined } from "@ant-design/icons";
import DataTable from "../../../components/Datatable/DataTable";
import DataTablePeriod from "../../../components/Datatable/DataTablePeriod";
import DataTableSearch from "../../../components/Datatable/DataTableSearch";
import { CloudSyncOutlined } from "@ant-design/icons";
import Title from "../Title";

class ApiLog extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      tableUuid: key(),
      columns: [],
    };
  }
  m = (key, prefix = "admin.apilog.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };
  componentDidMount() {
    this.setColumns();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.setColumns();
    }
  }

  setColumns = () => {
    this.setState({
      columns: [
        {
          title: "id",
          dataIndex: "id",
          sorter: true,
          render: (value) => <>{value}</>,
          width: "1%",
        },
        {
          title: this.m("datetime"),
          dataIndex: "datetime",
          sorter: true,
          render: (value) => {
            if (value) {
              const dt = new Date(value);
              return (
                <>
                  {dt.toLocaleDateString()}
                  <br />
                  {dt.toLocaleTimeString()}
                </>
              );
            }
          },
          width: "20%",
          date: true,
          filterDropdown: (props) => <DataTablePeriod {...props} />,
        },
        {
          text: true,
          title: this.m("username"),
          dataIndex: "username",
          sorter: true,
          render: (value) => (
            <div style={{ whiteSpace: "nowrap" }}>{value}</div>
          ),
          width: "15%",
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{ color: filtered ? "#1890ff" : undefined }}
            />
          ),
        },
        {
          text: true,
          title: this.m("path"),
          dataIndex: "path",
          sorter: true,
          render: (value) => (
            <div style={{ wordBreak: "break-all" }}>{value}</div>
          ),
          width: "15%",
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{ color: filtered ? "#1890ff" : undefined }}
            />
          ),
        },
        {
          title: "JSON",
          dataIndex: "json",
          sorter: true,
          width: "40%",
          render: (value) => (
            <div
              style={{
                wordBreak: "break-all",
                whiteSpace: "break-spaces",
                maxWidth: "100%",
              }}
            >
              {JSON.stringify(value)}
            </div>
          ),
        },
      ],
    });
  };

  render() {
    return (
      <div>
        <Title
          title={this.m("apilog")}
          url="/admin/apilog"
          icon={<CloudSyncOutlined />}
        />
        <Row>
          <Col span={24}>
            <DataTable
              uuid={this.state.tableUuid}
              columns={this.state.columns}
              path={"/admin/api/log/list"}
            />
          </Col>
        </Row>
      </div>
    );
  }
}

export default injectIntl(ApiLog);
